.header-top {
  background: #ffffff;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.0625em solid rgba(139, 141, 141, 0.2);;
    padding: 1em 0;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__select {
    display: flex;
    align-items: center;
    margin-right: 1.25em;
    &-text {
      font-size: 1em;
      line-height: 120%;
      color: rgba(16, 17, 18, 0.4);
      margin-right: 0.375em;
    }
  }
  .nice-select {
    width: auto;
    padding: 0 0.9375em 0 0;
    border: none;
    height: auto;
    background-image: url("../img/select-arrow.svg");
    background-position: center;
    background-size: 0.5em auto;
    font-size: unset;
    line-height: unset;
    &:after {
      top: 50%;
      right: 0;
      transform: translate(0, -50%) rotate(0);
      width: 0.5em;
      height: 0.25em;
      background-image: url("../img/select-arrow.svg");
      background-position: center;
      background-size: 0.5em 0.25em;
    }
    .current {
      font-family: Verdana;
      font-size: 1em;
      line-height: 120%;
      font-weight: 400;
      color: #101112;
    }
    .list {
      min-width: 11.25em;
      width: 100%;
      li {
        font-size: 1em;
      }
    }
  }
  .nice-select.open:after {
    transform: rotate(-180deg);
  }
  &__location {
    display: flex;
    align-items: center;
    margin-right: 1em;
    &-desc {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 0.625em;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 0.875em;
        line-height: 120%;
        color: #101112;
        margin-left: 0.375em;
        transition: all 0.5s;
      }
      &:hover {
        span {
          color: #A61F1F;
        }
      }
    }
  }
  &__office {
    margin-right: 1em;
    span {
      font-size: 0.875em;
      line-height: 120%;
      color: #8B8D8D;
    }
  }
  &__tel {
    span {
      font-size: 1em;
      line-height: 120%;
      font-weight: 700;
      color: #101112;
      transition: all 0.5s;
    }
    &:hover {
      span {
        color: #A61F1F;
      }
    }
  }
  &__entrance {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 0.875em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1em;
      line-height: 120%;
      color: #101112;
      margin-left: 0.375em;
      transition: all 0.5s;
    }
    &:hover {
      span {
        color: #A61F1F;
      }
    }
  }
  &__registration {
    margin-left: 1.25em;
    span {
      font-size: 1em;
      line-height: 120%;
      color: #101112;
      transition: all 0.5s;
    }
    &:hover {
      span {
        color: #A61F1F;
      }
    }
  }
  &__basket {
    margin-left: 1.5em;
    position: relative;
    i {
      display: block;
      width: 1.625em;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-quantity {
      position: absolute;
      top: 0;
      right: 0;
      width: 0.75em;
      height: 0.75em;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #9D0B1D;
      border-radius: 50%;
      span {
        font-size: 0.5625em;
        line-height: 100%;
        color: #ffffff;
      }
    }
    &:hover {
      i svg path {
        fill: #A61F1F;
      }
    }
  }
  &__burger {
    position: relative;
    z-index: 10;
    width: 1.875em;
    height: 1.375em;
    cursor: pointer;
    margin-left: 1.25em;
    display: none;
    span {
      position: absolute;
      right: 0;
      width: 100%;
      height: 0.125em;
      background: #101112;
      border-radius: 0.1875em;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span {
        background: #ffffff;
      }
      span:nth-child(1) {
        top: 0.625em;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 0.625em;
        transform: rotate(45deg);
      }
    }
  }
}
.header-center {
  background: #ffffff;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em 0;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    height: 2.75em;
    display: flex;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 15.25em;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 23.75em;
    margin-left: 0.625em;
    p {
      font-size: 0.875em;
      line-height: 120%;
      color: #9D0B1D;
    }
  }
  &__search {
    width: 21.875em;
    height: 100%;
    position: relative;
    input {
      width: 100%;
      height: 100%;
      border: 0.0625em solid rgba(139, 141, 141, 0.4);
      padding: 0 2.875em 0 0.75em;
      color: #101112;
      &::placeholder {
        opacity: 0.4;
      }
    }
    span {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 0.625em;
      transform: translate(0, -50%);
      width: 1.5em;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
  &__mail {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #9D0B1D;
    margin-left: 1.25em;
    padding: 0 1em;
    i {
      flex-shrink: 0;
      width: 1.4375em;
      margin-left: 0.75em;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 1em;
      line-height: 120%;
      font-weight: 700;
      color: #101112;
    }
    &:hover {
      background: #9D0B1D;
      span {
        color: #ffffff;
      }
      svg path {
        fill: #ffffff;
      }
    }
  }
}
.header-bottom {
  padding: 1em;
  border-bottom: 0.0625em solid #ffffff;
  background: #101112;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__products {
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
    }
    &-icon {
      position: relative;
      z-index: 2;
      width: 1.875em;
      height: 1.375em;
      span {
        position: absolute;
        left: 0;
        height: 0.125em;
        background: #ffffff;
        border-radius: 0.1875em;
      }
      span:nth-child(1) {
        top: 0;
        width: 100%;
      }
      span:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
        width: 100%;
      }
      span:nth-child(3) {
        bottom: 0;
        width: 1.1875em;
      }
    }
    &-desc {
      margin-left: 1em;
      span {
        font-size: 1em;
        line-height: 120%;
        font-weight: 700;
        color: #ffffff;
        text-transform: uppercase
      }
    }
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      font-size: 1em;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
      text-transform: uppercase;
      &:not(:first-child) {
        margin-left: 5.3125em;
      }
      &:hover {
        color: #A61F1F;
      }
    }
  }
}
.header-mob {
  position: fixed;
  z-index: 8;
  top: 0;
  left: -105%;
  width: 100%;
  height: 100%;
  background: #101112;
  padding: 9.375em 0.9375em 1.875em 0.9375em;
  transition: all 0.5s;
  overflow-x: auto;
  &--active {
    left: 0;
  }
  &::-webkit-scrollbar {
    width: 0.1875em;
  }
  &::-webkit-scrollbar-track {
    background: #ffffff;
    border-radius: 0.1875em;
  }
  &::-webkit-scrollbar-thumb {
    background: #a61f1f;
    border-radius: 0.1875em;
  }
  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
  }
  &__list {
    li {
      font-size: 1em;
      line-height: 120%;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 1.5em;
      }
      &:hover {
        color: #A61F1F;
      }
    }
  }
  &__tel {
    margin-top: 1.5em;
    span {
      font-size: 1em;
      line-height: 120%;
      color: #ffffff;
      transition: all 0.5s;
    }
    &:hover {
      span {
        color: #A61F1F;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header-top {
    &__location {
      &-desc {
        i {
          width: 1.25em;
        }
        span {
          display: none;
        }
      }
    }
    &__tel {
      display: none;
    }
    &__burger {
      display: block;
    }
  }
  .header-center {
    &__right {
      height: auto;
    }
    &__logo {
      width: 12.5em;
    }
    &__text {
      display: none;
    }
    &__search {
      width: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      input {
        display: none;
      }
      span {
        position: static;
        display: block;
        transform: translate(0);
      }
    }
    &__mail {
      border: none;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        margin: 0;
      }
      span {
        display: none;
      }
      &:hover {
        background: transparent;
        i svg path {
          fill: #A61F1F;
        }
      }
    }
  }
  .header-bottom {
    &__list {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .header-top {
    &__left {
      display: block;
      margin-right: 0.75em;
    }
    &__select {
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    &__select {
      &-text {
        font-size: 0.875em;
      }
      .nice-select {
        .current {
          font-size: 0.875em;
        }
        .list {
          li {
            font-size: 0.875em;
          }
        }
      }
    }
    &__entrance {
      span {
        font-size: 0.875em;
      }
    }
    &__registration {
      margin-left: 0.75em;
      span {
        font-size: 0.875em;
      }
    }
    &__basket {
      margin-left: 0.75em;
    }
    &__burger {
      margin-left: 0.75em;
    }
  }
  .header-center {
    &__logo {
      width: 9.375em;
    }
  }
  .header-bottom {
    &__products {
      &-desc {
        span {
          font-size: 0.875em;
        }
      }
    }
  }
}